//  
type ThemeState = {
  themeId: number;
};

type ChangeThemeAction = {
  type: "SET_THEME_ID";
  id: number;
};

//  
const initState: ThemeState = {
  themeId: 1,
};

// 
export const themeReducer = (
  state = initState,
  action: ChangeThemeAction
): ThemeState => {
  switch (action.type) {
    case "SET_THEME_ID":
      return { ...state, themeId: action.id }; //  themeId
    default:
      return state;
  }
};

//    themeId
export const changeThemeId = (id: number): ChangeThemeAction => ({
  type: "SET_THEME_ID",
  id,
});
